import React from 'react';
import './App.css';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'

import Home from './screens/Home'
import Education from './screens/Education'
import Pro from './screens/Pro'
import Perso from './screens/Perso'
import Projet from './screens/Projets'
import GameZone from './screens/GameZone'
import Masques from './screens/Masques'
import Morningnews from './screens/Morningnews'
import Ticketac from './screens/Ticketac'
import Blackboard from './screens/Blackboard'
import Mymoviz from './screens/Mymoviz'
import Weatherapp from './screens/Weatherapp'
import Bikeshop from './screens/Bikeshop'
import Pendu from './screens/Pendu'
import Pingpong from './screens/Pingpong'
import NotFound from './screens/NotFound'


export default function App() {
    return (

        <Router basename="/Portfolio">
            <Switch>
                <Route path="/">
                    <Home/>
                </Route>
                <Route path="/education">
                    <Education/>
                </Route>
                <Route path="/pro">
                    <Pro/>
                </Route>
                <Route path="/perso">
                    <Perso/>
                </Route>
                <Route path="/projets">
                    <Projet/>
                </Route>
                <Route path="/game">
                    <GameZone/>
                </Route>
                <Route path="/masques">
                    <Masques/>
                </Route>
                <Route path="/morningnews">
                    <Morningnews/>
                </Route>
                <Route path="/ticketac">
                    <Ticketac/>
                </Route>
                <Route path="/blackboard">
                    <Blackboard/>
                </Route>
                <Route path="/mymoviz">
                    <Mymoviz/>
                </Route>
                <Route path="/weatherapp">
                    <Weatherapp/>
                </Route>
                <Route path="/bikeshop">
                    <Bikeshop/>
                </Route>
                <Route path="/pendu">
                    <Pendu/>
                </Route>
                <Route path="/pingpong">
                    <Pingpong/>
                </Route>
                <Route>
                    <NotFound/>
                </Route>
            </Switch>
        </Router>
    );
}
